import React, { useState } from "react";
import { useDispatch } from "react-redux";
import {
  Text,
  View,
  TextInput,
  Button,
  StyleSheet,
  SafeAreaView,
} from "react-native";
// import { useForm, Controller } from "react-hook-form";
import { Formik } from "formik";
import { addShoppingList } from "../reducers/shoppinglistsSlice";
import { TouchableOpacity } from "react-native-web";

const AddShoppingListForm = () => {
  const [text, setText] = useState("");
  const [itemQuantity, setItemQuantity] = useState("");
  const [itemPrice, setItemPrice] = useState("");
  const dispatch = useDispatch();

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      addShoppingList({ title: text, quantity: itemQuantity, price: itemPrice })
    );
    setText("");
    setItemQuantity("");
    setItemPrice("");
  };

  return (
    <SafeAreaView>
      <Formik onSubmit={submitHandler}>
        <View>
          <View style={styles.listsContainer}>
            <TextInput
              placeholder="Add item"
              onChangeText={setText}
              value={text}
              style={styles.listInput}
            />
            <TextInput
              placeholder="Add quantity by number"
              onChangeText={setItemQuantity}
              value={itemQuantity}
              style={styles.listInput}
              textContentType="number"
            />
            <TextInput
              placeholder="Add price by number"
              onChangeText={setItemPrice}
              value={itemPrice}
              style={styles.listInput}
              textContentType="number"
            />
          </View>
          <TouchableOpacity onPress={submitHandler}>
            <View style={styles.addBtn}>
              <Text style={styles.addBtnText}>Add</Text>
            </View>
          </TouchableOpacity>
        </View>
      </Formik>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  listsContainer: {
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-start",
    justifyContent: "flex-start",
    flex: 1,
  },

  listInput: {
    fontSize: 22,
    lineHeight: 1.5,
    marginBottom: 10,
    paddingLeft: 5,
    color: "black",
    width: "100%",
  },

  addBtn: {
    backgroundColor: "#0A004D",
    borderRadius: 10,
    maxWidth: "100%",
  },

  addBtnText: {
    color: "white",
    fontWeight: "bold",
    fontSize: 20,
    textAlign: "center",
    padding: 10,
  },
});

export default AddShoppingListForm;
